#sound-control {
    right:auto;
    @include center(true, false);
    bottom:10px;
    padding:0;
    position:fixed;
    width:100%;
    max-width:800px;
    display:none;
    
    .btn-large {
      background: #3d4444;
      border:5px solid #85a2a7;
      color:#acfefe;
      position:relative;    
    }
    i {
      color:#acfefe;
      @include center(true, true);
      font-weight:900;
      font-size:2.2em;
    }
